* {
  box-sizing: border-box;
}

body {
  margin: 0;
}

h1,
h2,
p {
  margin: 0;
  padding: 0;
}

.App {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100vw;
  height: 100vh;
  padding-top: 30px;
  background-color: #f5f4eb;
  color: #4c3327;

  &__title {
    margin-bottom: 10px;
    font-size: 60px;
  }

  &__input-title {
    display: block;
    width: 100%;
    padding: 12px 20px;
    margin-bottom: 10px;
    box-sizing: border-box;

    &:focus {
      border: 3px solid #555;
    }
  }

  &__form-title {
    text-align: center;
    margin-bottom: 20px;
  }

  &__select {
    display: block;
    width: 100%;
    padding: 16px 20px;
    border: none;
    border-radius: 4px;
    background-color: #fff;
    margin-bottom: 8px;
  }

  &__label {
    margin-right: 15px;
    line-height: 32px;
  }

  &__radio {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    border-radius: 50%;
    width: 16px;
    height: 16px;

    border: 2px solid #999;
    transition: 0.2s all linear;
    margin-right: 5px;

    position: relative;
    top: 4px;

    &:checked {
      border: 4px solid black;
    }
  }

  &__button {
    background-color: #555;
    border-radius: 5px;
    color: #f1f1f1;
    padding: 10px;
  }

  &__list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
  }

  &__item {
    border: 1px solid #4c3327;
    background-color: #98bb81a6;
    border-radius: 5px;
    padding: 20px;
    margin: 10px;
    box-shadow: rgba(0, 0, 0, 0.15) 0 15px 25px, rgba(0, 0, 0, 0.05) 0 5px 10px;
  }

  &__error {
    color: tomato;
    margin-bottom: 5px;
  }
}
